@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.3/css/all.min.css);
* {
  margin: 0;
}
body {
  margin: 0;
  font-family: Nunito !important;
  background: #fff !important;
}

code {
  font-family:Nunito !important;
}

* {
  font-family: 'Nunito' !important;

font-style: 'Regular';
font-size: 16px;

  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

i {
  font-family: 'Nunito', sans-serif !important;
  cursor: pointer;
}

a {
  text-decoration: none;
}
.search-input{
height: 50px;
padding: 10px;
border: none;
background: #FFFFFF;
box-shadow: 4px 0px 10px rgba(0, 0, 0, 0.2);
}
.search-input:focus {
  outline: none;
}
.title{
margin-bottom: 5px;
font-style: normal;
font-weight: 700;
font-size: 24px;
line-height: 33px;
}

.page{
  background: #FFFFFF;
box-shadow: 4px 0px 10px rgba(0, 0, 0, 0.2);

}
.btn {
  width: 52%;
  padding: 10px;
  margin: 10px;
  background: #800080;
  color: #fff;
  outline: none;
  border: none;
  border-radius: 8px;
  font-size: 18px;
  cursor: pointer;
}

.btn:hover {
  background: #8b008b;
  color: #ffffff;
}

:root {
  --primary-color: #17a2b8;
  --dark-color: #343a40;
  --light-color: #f4f4f4;
  --danger-color: #dc3545;
  --success-color: #28a745;
}

.alert {
  padding: 0.8rem;
  margin: 1rem 0;
  opacity: 0.9;
  background: #f4f4f4;
  background: var(--light-color);
  color: #333;
}

.alert-primary {
  background: #17a2b8;
  background: var(--primary-color);
  color: #fff;
}

.alert-light {
  background: #f4f4f4;
  background: var(--light-color);
  color: #333;
}

.alert-dark {
  background: #343a40;
  background: var(--dark-color);
  color: #fff;
}

.alert-danger {
  background: #dc3545;
  background: var(--danger-color);
  color: #fff;
}

.alert-success {
  background: #28a745;
  background: var(--success-color);
  color: #fff;
}

/* Navbar */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.7rem 2rem;
  position: fixed;
  z-index: 1;
  width: 100%;
  top: 0;
  opacity: 0.9;
  background: #800080;
}

.navbar .nav-links {
  display: flex;
  list-style: none;
}

.navbar a {
  color: #fff;
  padding: 1rem;
  margin: 0 1rem;
}

.navbar .welcome span {
  margin-right: 0.6rem;
}

/* Landing Page */
.landing {
  position: relative;
  /* background: url('./img/landing.jpg') no-repeat center 80% / cover; */
  height: 100vh;
}

.dark-overlay {
  background-color: rgba(0, 0, 0, 0.4);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.landing-inner {
  color: #fff;
  height: 60%;
  width: 60%;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.landing-inner h1 {
  font-size: 60px;
}
.landing-inner p {
  font-size: 18px;
  max-width: 60%;
}

.landing-inner .buttons {
  margin-top: 1rem;
}

.register-form,
.login-form {
  width: 80%;
  text-align: center;
  margin: 6rem auto;
  justify-content: center;
}

.form input {
  width: 50%;
  padding: 10px;
  margin: 10px;
}

.lead {
  font-size: 25px;
}

.heading {
  font-size: 40px;
}

.link a {
  color: #800080;
}

/* Mobile Styles */
@media (max-width: 700px) {
  /* Navbar */
  .navbar {
    display: block;
    text-align: center;
  }

  .navbar ul {
    text-align: center;
    justify-content: center;
  }

  .navbar h1 {
    margin-bottom: 1rem;
  }

  .navbar .welcome {
    display: none;
  }
}
.main {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 90%;
  height: 50vh;
  margin-left: auto;
  margin-top: 2%;
  margin-right: auto;
  background: #fff;
  border: none;
}
.container {
  min-height: 59vh;
  width: 100%;
  border-radius: 0px;
  background: #f5f5f5;
  margin-top: 0.5%;
  box-shadow: 5.29353px 0px 13.2338px rgba(0, 0, 0, 0.2);
  border: none;
}

.main {
  width: 400px;
  height: 60px;
  padding-left: '10px';
  background: #ffffff;
  border: 2px solid rgba(0, 0, 0, 0.4);
  border-radius: 10px 10px 0px 0px;
  font-family: 'Nunito';
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 25px;
}

.main:focus {
  outline: none !important;
  border-color: #18a0fb;
}
.mainTwo{
  width: 400px;
  height: 60px;
  padding-left: '10px';
  background: #ffffff;
  border: 2px solid red;
  border-radius: 10px 10px 0px 0px;
  font-family: 'Nunito';
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 25px;
}
.mainTwo:focus {
  outline: none !important;
  border-color: red;
}
.btnTwo {
  display: flex;
  justify-content: center;
  width: 150px;
  height: 60px;
  cursor: pointer;
  border: none;
  color: '#fff !important';
  /* sea blue */

  background: linear-gradient(90deg, #2b5876 0%, #4e4376 100%);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
}
.circle {
  display: flex;
  justify-content: center;
  position: fixed;
  align-items: center;
  margin-top: -44px;
  margin-left: 200px;
  margin-right: auto;
  background: #ffffff;
  /* drop shadow */

  box-shadow: 4px 0px 10px rgba(0, 0, 0, 0.2);
  border-radius: 50%;
  width: 80px;
  height: 80px;
}
.paragraph-Style{
  font-size: 30px !important;
}

